<template>
    <div id="edit-employee">
        <h3>Edit Employee</h3>
        <div class="row">
            <form @submit.prevent="updateEmployee" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="employee_id" required disabled>
                        <label for="employee_id" class="active">Employee ID#</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        <label for="name" class="active">Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="dept" required>
                        <label for="dept" class="active">Department</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="position" required>
                        <label for="employee_id" class="active">Position</label>
                    </div>
                </div>
                <button class="btn" type="submit">Update</button>
                <router-link to="/" class="btn grey">CANCEL</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from "./firebaseInit"
export default {
    name: "edit-employee",
    data() {
        return {
            employee_id:null,
            name:null,
            dept:null,
            position:null
        }
    },
    beforeRouteEnter(to, from, next){
        db.collection('employee').where('employee_id','==',to.params.employee_id).get()
            .then(querySnapShot => {
                querySnapShot.forEach(doc => {
                   next(vm => {
                        vm.employee_id = doc.data().employee_id
                        vm.name = doc.data().name
                        vm.dept = doc.data().dept,
                        vm.position = doc.data().position
                   })
                })
            })
            .catch(err => console.log(err))
    },
    watch: {
        '$route':'fetchData'
    },
    methods: {
        fetchData(){
            db.collection('employee').where('employee_id','==',this.$route.params.employee_id).get()
                .then(querySnapShot => {
                    querySnapShot.forEach(doc => {
                        this.employee_id = doc.data().employee_id
                        this.name = doc.data().name
                        this.dept = doc.data().dept,
                        this.position = doc.data().position
                    })
                })
                .catch(err => console.log(err))
        },
        updateEmployee() {
            db.collection('employee').where('employee_id','==',this.employee_id).get()
                .then(querySnapShot => {
                    querySnapShot.forEach(doc => {
                        doc.ref.update({
                            employee_id: this.employee_id,
                            name: this.name,
                            dept: this.dept,
                            position:this.position
                        })
                    })
                })
                .then(() => {
                    this.$router.push({name:'view-employee', params:{employee_id:this.employee_id}})
                })
        }
    }
}
</script>